.avatar{
  animation: load-down 600ms ease-in 200ms;
  animation-fill-mode: forwards;
  transform: translateY(-30px);
  opacity: 0;
}

.name{
  animation: load-down 600ms ease-in 200ms;
  animation-fill-mode: forwards;
  transform: translateY(-30px);
  opacity: 0;
  font-size: 2.8rem;
  display:flex;
}

.main-name{
  font-size:2.8rem;
  margin-left:2%;
  color:white;
  animation: load-down 600ms ease-in 200ms;
  animation-fill-mode: forwards;
  transform: translateY(-30px);
  opacity: 0;

}

.title {
  animation:load-up 600ms ease-in 200ms;
  animation-fill-mode: forwards;
  transform: translateY(30px);
  opacity: 0;
  color:white;
}

.icons {
  animation-name: load-up-icons;
  animation-duration: 600ms;
  animation-timing-function: ease-in;
  transform: translateY(30px);
  opacity: 1;
  animation-delay: 200ms;
}

.first-icon{
  animation: load-in-each-icon 500ms ease-in 500ms;
  opacity: 0;
  animation-fill-mode: forwards;
}

.second-icon {
  animation: load-in-each-icon 500ms ease-in 750ms;
  opacity: 0;
  animation-fill-mode: forwards;
}

.third-icon {
  animation: load-in-each-icon 500ms ease-in 1s;
  opacity: 0;
  animation-fill-mode: forwards;
}

.fourth-icon {
  animation: load-in-each-icon 500ms ease-in 1.25s;
  opacity: 0;
  animation-fill-mode: forwards;
}


.to-work-container{
  margin-top:9%;
  cursor:pointer;

}

.arrow {
  position: absolute;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.arrow:first-child {
  animation: move 3s ease-out 1s infinite;

}

.arrow:nth-child(2) {
  animation: move 3s ease-out 2s infinite;

}

.arrow:before,
.arrow:after {
  content: ' ';
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
}

.arrow:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.arrow:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

.to-work {
  display: block;
  margin-top: 75px;
  margin-left: -14px;
  font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: .25;
  animation: pulse 2s linear alternate infinite;
}

@keyframes load-down {
  0% {
    transform: translateY(-30px);
    opacity: 0
  }

  70% {
    transform: translateY(4px);
  }

  100% {
    transform: translateY(0px);
    opacity: 1
  }
}

@keyframes load-up{
  0% {
    transform: translateY(30px);
    opacity: 0;
  }

  70% {
    transform: translateY(-4px);
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes load-up-icons{
  0% {
    transform: translateY(30px);
    opacity: 0;
  }

  70% {
    transform: translateY(-4px);
  }

  100% {
    transform: translateY(0px);
  }
}


@keyframes load-in-each-icon{
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes move {
  25% {
    opacity: 1;

  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}